namespace pixi_projection {
    export class Projection {

        constructor(legacy: PIXI.TransformBase, enable: boolean = true) {
            this.legacy = legacy as PIXI.TransformStatic;

            if (enable) {
                this.enabled = true;
            }

            // sorry for hidden class, it would be good to have special projection field in official pixi
            this.legacy.proj = this;
        }

        legacy: PIXI.TransformStatic;

        _enabled: boolean = false;

        get enabled() {
            return this._enabled;
        }

        set enabled(value: boolean) {
            this._enabled = value;
        }

        clear() {
        }
    }
}
namespace pixi_projection.webgl {
	export class BatchBuffer {
		vertices: ArrayBuffer;
		float32View: Float32Array;
		uint32View: Uint32Array;


		constructor(size: number) {
			this.vertices = new ArrayBuffer(size);

			/**
			 * View on the vertices as a Float32Array for positions
			 *
			 * @member {Float32Array}
			 */
			this.float32View = new Float32Array(this.vertices);

			/**
			 * View on the vertices as a Uint32Array for uvs
			 *
			 * @member {Float32Array}
			 */
			this.uint32View = new Uint32Array(this.vertices);
		}

		/**
		 * Destroys the buffer.
		 *
		 */
		destroy() {
			this.vertices = null;
		}
	}
}
namespace pixi_projection.webgl {
	export function generateMultiTextureShader(vertexSrc: string, fragmentSrc: string, gl: WebGLRenderingContext, maxTextures: number) {
		fragmentSrc = fragmentSrc.replace(/%count%/gi, maxTextures + '');
		fragmentSrc = fragmentSrc.replace(/%forloop%/gi, generateSampleSrc(maxTextures));

		const shader = new PIXI.Shader(gl, vertexSrc, fragmentSrc);

		const sampleValues = new Int32Array(maxTextures);

		for (let i = 0; i < maxTextures; i++) {
			sampleValues[i] = i;
		}

		shader.bind();
		shader.uniforms.uSamplers = sampleValues;

		return shader;
	}

	function generateSampleSrc(maxTextures: number) {
		let src = '';

		src += '\n';
		src += '\n';

		for (let i = 0; i < maxTextures; i++) {
			if (i > 0) {
				src += '\nelse ';
			}

			if (i < maxTextures - 1) {
				src += `if(textureId == ${i}.0)`;
			}

			src += '\n{';
			src += `\n\tcolor = texture2D(uSamplers[${i}], textureCoord);`;
			src += '\n}';
		}

		src += '\n';
		src += '\n';

		return src;
	}
}
