namespace pixi_projection.webgl {
	import BaseTexture = PIXI.BaseTexture;
	import ObjectRenderer = PIXI.ObjectRenderer;
	import settings = PIXI.settings;
	import GLBuffer = PIXI.glCore.GLBuffer;
	import VertexArrayObject = PIXI.glCore.VertexArrayObject;

	import WebGLRenderer = PIXI.WebGLRenderer;
	import Sprite = PIXI.Sprite;

	import premultiplyTint = PIXI.utils.premultiplyTint;
	import premultiplyBlendMode = PIXI.utils.premultiplyBlendMode;

	let TICK = 0;

	export class BatchGroup {
		textures: Array<BaseTexture> = [];
		textureCount = 0;
		ids: Array<Number> = [];
		size = 0;
		start = 0;
		blend = PIXI.BLEND_MODES.NORMAL;
		uniforms: any = null;
	}

	export abstract class MultiTextureSpriteRenderer extends ObjectRenderer {
		shaderVert = '';
		shaderFrag = '';
		MAX_TEXTURES_LOCAL = 32;

		abstract createVao(vertexBuffer: GLBuffer): PIXI.glCore.VertexArrayObject;

		abstract fillVertices(float32View: Float32Array, uint32View: Uint32Array, index: number, sprite: any, argb: number, textureId: number);

		getUniforms(spr: PIXI.Sprite): any {
			return null;
		}

		syncUniforms(obj: any) {
			if (!obj) return;
			let sh = this.shader;
			for (let key in obj) {
				sh.uniforms[key] = obj[key];
			}
		}

		vertSize = 5;
		vertByteSize = this.vertSize * 4;
		size = settings.SPRITE_BATCH_SIZE;
		buffers: Array<BatchBuffer>;

		indices: Uint16Array;

		shader: PIXI.Shader;

		currentIndex = 0;
		groups: Array<BatchGroup>;
		sprites: Array<Sprite> = [];

		indexBuffer: GLBuffer;
		vertexBuffers: Array<GLBuffer> = [];
		vaos: Array<VertexArrayObject> = [];
		vao: VertexArrayObject;
		vaoMax = 2;
		vertexCount = 0;

		MAX_TEXTURES = 1;

		/**
		 * @param {PIXI.WebGLRenderer} renderer - The renderer this sprite batch works for.
		 */
		constructor(renderer: WebGLRenderer) {
			super(renderer);

			this.indices = utils.createIndicesForQuads(this.size);

			this.groups = [];
			for (let k = 0; k < this.size; k++) {
				this.groups[k] = new BatchGroup();
			}

			this.vaoMax = 2;
			this.vertexCount = 0;

			this.renderer.on('prerender', this.onPrerender, this);
		}

		/**
		 * Sets up the renderer context and necessary buffers.
		 *
		 * @private
		 */
		onContextChange() {
			const gl = this.renderer.gl;

			this.MAX_TEXTURES = Math.min(this.MAX_TEXTURES_LOCAL, this.renderer.plugins['sprite'].MAX_TEXTURES);

			// generate generateMultiTextureProgram, may be a better move?
			this.shader = generateMultiTextureShader(this.shaderVert, this.shaderFrag, gl, this.MAX_TEXTURES);

			this.indexBuffer = GLBuffer.createIndexBuffer(gl, this.indices, gl.STATIC_DRAW);

			// we use the second shader as the first one depending on your browser may omit aTextureId
			// as it is not used by the shader so is optimized out.

			this.renderer.bindVao(null);

			const attrs = this.shader.attributes;

			for (let i = 0; i < this.vaoMax; i++) {
				/* eslint-disable max-len */
				const vertexBuffer = this.vertexBuffers[i] = GLBuffer.createVertexBuffer(gl, null, gl.STREAM_DRAW);
				/* eslint-enable max-len */

				// build the vao object that will render..
				this.vaos[i] = this.createVao(vertexBuffer);
			}

			if (!this.buffers) {
				this.buffers = [];
				for (let i = 1; i <= utils.nextPow2(this.size); i *= 2) {
					this.buffers.push(new BatchBuffer(i * 4 * this.vertByteSize));
				}
			}

			this.vao = this.vaos[0];
		}

		/**
		 * Called before the renderer starts rendering.
		 *
		 */
		onPrerender() {
			this.vertexCount = 0;
		}

		/**
		 * Renders the sprite object.
		 *
		 * @param {PIXI.Sprite} sprite - the sprite to render when using this spritebatch
		 */
		render(sprite: Sprite) {
			// TODO set blend modes..
			// check texture..
			if (this.currentIndex >= this.size) {
				this.flush();
			}

			// get the uvs for the texture

			// if the uvs have not updated then no point rendering just yet!
			if (!(sprite as any)._texture._uvs) {
				return;
			}
			if (!(sprite as any)._texture.baseTexture) {
				//WTF, Rpgmaker MV?
				return;
			}

			// push a texture.
			// increment the batchsize
			this.sprites[this.currentIndex++] = sprite;
		}

		/**
		 * Renders the content and empties the current batch.
		 *
		 */
		flush() {
			if (this.currentIndex === 0) {
				return;
			}

			const gl = this.renderer.gl;
			const MAX_TEXTURES = this.MAX_TEXTURES;

			const np2 = utils.nextPow2(this.currentIndex);
			const log2 = utils.log2(np2);
			const buffer = this.buffers[log2];

			const sprites = this.sprites;
			const groups = this.groups;

			const float32View = buffer.float32View;
			const uint32View = buffer.uint32View;

			// const touch = 0;// this.renderer.textureGC.count;

			let index = 0;
			let nextTexture: any;
			let currentTexture: BaseTexture;
			let currentUniforms: any = null;
			let groupCount = 1;
			let textureCount = 0;
			let currentGroup = groups[0];
			let vertexData;
			let uvs;
			let blendMode = premultiplyBlendMode[
				(sprites[0] as any)._texture.baseTexture.premultipliedAlpha ? 1 : 0][sprites[0].blendMode];

			currentGroup.textureCount = 0;
			currentGroup.start = 0;
			currentGroup.blend = blendMode;

			TICK++;

			let i;

			for (i = 0; i < this.currentIndex; ++i) {
				// upload the sprite elemetns...
				// they have all ready been calculated so we just need to push them into the buffer.

				// upload the sprite elemetns...
				// they have all ready been calculated so we just need to push them into the buffer.
				const sprite = sprites[i] as any;

				nextTexture = sprite._texture.baseTexture;

				const spriteBlendMode = premultiplyBlendMode[Number(nextTexture.premultipliedAlpha)][sprite.blendMode];

				if (blendMode !== spriteBlendMode) {
					// finish a group..
					blendMode = spriteBlendMode;

					// force the batch to break!
					currentTexture = null;
					textureCount = MAX_TEXTURES;
					TICK++;
				}

				const uniforms = this.getUniforms(sprite);
				if (currentUniforms !== uniforms) {
					currentUniforms = uniforms;

					currentTexture = null;
					textureCount = MAX_TEXTURES;
					TICK++;
				}

				if (currentTexture !== nextTexture) {
					currentTexture = nextTexture;

					if (nextTexture._enabled !== TICK) {
						if (textureCount === MAX_TEXTURES) {
							TICK++;

							textureCount = 0;

							currentGroup.size = i - currentGroup.start;

							currentGroup = groups[groupCount++];
							currentGroup.textureCount = 0;
							currentGroup.blend = blendMode;
							currentGroup.start = i;
							currentGroup.uniforms = currentUniforms;
						}

						nextTexture._enabled = TICK;
						nextTexture._virtalBoundId = textureCount;

						currentGroup.textures[currentGroup.textureCount++] = nextTexture;
						textureCount++;
					}
				}

				const alpha = Math.min(sprite.worldAlpha, 1.0);
				// we dont call extra function if alpha is 1.0, that's faster
				const argb = alpha < 1.0 && nextTexture.premultipliedAlpha ? premultiplyTint(sprite._tintRGB, alpha)
					: sprite._tintRGB + (alpha * 255 << 24);

				this.fillVertices(float32View, uint32View, index, sprite, argb, nextTexture._virtalBoundId);

				index += this.vertSize * 4;
			}

			currentGroup.size = i - currentGroup.start;

			if (!settings.CAN_UPLOAD_SAME_BUFFER) {
				// this is still needed for IOS performance..
				// it really does not like uploading to the same buffer in a single frame!
				if (this.vaoMax <= this.vertexCount) {
					this.vaoMax++;

					const attrs = this.shader.attributes;

					/* eslint-disable max-len */
					const vertexBuffer = this.vertexBuffers[this.vertexCount] = GLBuffer.createVertexBuffer(gl, null, gl.STREAM_DRAW);
					/* eslint-enable max-len */

					this.vaos[this.vertexCount] = this.createVao(vertexBuffer);
				}

				this.renderer.bindVao(this.vaos[this.vertexCount]);

				this.vertexBuffers[this.vertexCount].upload(buffer.vertices, 0, false);

				this.vertexCount++;
			}
			else {
				// lets use the faster option, always use buffer number 0
				this.vertexBuffers[this.vertexCount].upload(buffer.vertices, 0, true);
			}

			currentUniforms = null;

			// / render the groups..
			for (i = 0; i < groupCount; i++) {
				const group = groups[i];
				const groupTextureCount = group.textureCount;

				if (group.uniforms !== currentUniforms) {
					this.syncUniforms(group.uniforms);
				}

				for (let j = 0; j < groupTextureCount; j++) {
					this.renderer.bindTexture(group.textures[j], j, true);

					const v = this.shader.uniforms.samplerSize;
					if (v) {
						v[0] = group.textures[j].realWidth;
						v[1] = group.textures[j].realHeight;
						this.shader.uniforms.samplerSize = v;
					}
				}

				// set the blend mode..
				this.renderer.state.setBlendMode(group.blend);

				gl.drawElements(gl.TRIANGLES, group.size * 6, gl.UNSIGNED_SHORT, group.start * 6 * 2);
			}

			// reset elements for the next flush
			this.currentIndex = 0;
		}

		/**
		 * Starts a new sprite batch.
		 */
		start() {
			this.renderer.bindShader(this.shader);

			if (settings.CAN_UPLOAD_SAME_BUFFER) {
				// bind buffer #0, we don't need others
				this.renderer.bindVao(this.vaos[this.vertexCount]);

				this.vertexBuffers[this.vertexCount].bind();
			}
		}

		/**
		 * Stops and flushes the current batch.
		 *
		 */
		stop() {
			this.flush();
		}

		/**
		 * Destroys the SpriteRenderer.
		 *
		 */
		destroy() {
			for (let i = 0; i < this.vaoMax; i++) {
				if (this.vertexBuffers[i]) {
					this.vertexBuffers[i].destroy();
				}
				if (this.vaos[i]) {
					this.vaos[i].destroy();
				}
			}

			if (this.indexBuffer) {
				this.indexBuffer.destroy();
			}

			this.renderer.off('prerender', this.onPrerender, this);

			super.destroy();

			if (this.shader) {
				this.shader.destroy();
				this.shader = null;
			}

			this.vertexBuffers = null;
			this.vaos = null;
			this.indexBuffer = null;
			this.indices = null;

			this.sprites = null;

			for (let i = 0; i < this.buffers.length; ++i) {
				this.buffers[i].destroy();
			}
		}
	}
}
namespace pixi_projection {
    import PointLike = PIXI.PointLike;

    const p = [new PIXI.Point(), new PIXI.Point(), new PIXI.Point(), new PIXI.Point()];
    const a = [0, 0, 0, 0];

    export abstract class Surface implements IWorldTransform {
        surfaceID = "default";

        _updateID: number = 0;

        vertexSrc: string = "";
        fragmentSrc: string = "";

        fillUniforms(uniforms: any) {

        }

        clear() {

        }

        /**
         * made for bilinear, other things will need adjustments, like test if (0) is inside
         * @param {ArrayLike<number>} v
         * @param out
         * @param {PIXI.Matrix} after
         */
        boundsQuad(v: ArrayLike<number>, out: any, after?: PIXI.Matrix) {
            let minX = out[0], minY = out[1];
            let maxX = out[0], maxY = out[1];
            for (let i = 2; i < 8; i += 2) {
                if (minX > out[i]) minX = out[i];
                if (maxX < out[i]) maxX = out[i];
                if (minY > out[i + 1]) minY = out[i + 1];
                if (maxY < out[i + 1]) maxY = out[i + 1];
            }

            p[0].set(minX, minY);
            this.apply(p[0], p[0]);
            p[1].set(maxX, minY);
            this.apply(p[1], p[1]);
            p[2].set(maxX, maxY);
            this.apply(p[2], p[2]);
            p[3].set(minX, maxY);
            this.apply(p[3], p[3]);

            if (after) {
                after.apply(p[0], p[0]);
                after.apply(p[1], p[1]);
                after.apply(p[2], p[2]);
                after.apply(p[3], p[3]);
                out[0] = p[0].x;
                out[1] = p[0].y;
                out[2] = p[1].x;
                out[3] = p[1].y;
                out[4] = p[2].x;
                out[5] = p[2].y;
                out[6] = p[3].x;
                out[7] = p[3].y;
            } else {
                for (let i = 1; i <= 3; i++) {
                    if (p[i].y < p[0].y || p[i].y == p[0].y && p[i].x < p[0].x) {
                        let t = p[0];
                        p[0] = p[i];
                        p[i] = t;
                    }
                }

                for (let i = 1; i <= 3; i++) {
                    a[i] = Math.atan2(p[i].y - p[0].y, p[i].x - p[0].x);
                }
                for (let i = 1; i <= 3; i++) {
                    for (let j = i + 1; j <= 3; j++) {
                        if (a[i] > a[j]) {
                            let t = p[i];
                            p[i] = p[j];
                            p[j] = t;
                            let t2 = a[i];
                            a[i] = a[j];
                            a[j] = t2;
                        }
                    }
                }

                out[0] = p[0].x;
                out[1] = p[0].y;
                out[2] = p[1].x;
                out[3] = p[1].y;
                out[4] = p[2].x;
                out[5] = p[2].y;
                out[6] = p[3].x;
                out[7] = p[3].y;

                if ((p[3].x - p[2].x) * (p[1].y - p[2].y) - (p[1].x - p[2].x) * (p[3].y - p[2].y) < 0) {
                    //triangle!!!
                    out[4] = p[3].x;
                    out[5] = p[3].y;
                    return;
                }
            }
        }

        abstract apply(pos: PointLike, newPos: PointLike): PointLike;

        //TODO: remove props
        abstract applyInverse(pos: PointLike, newPos: PointLike): PointLike;
    }
}
namespace pixi_projection {
	import PointLike = PIXI.PointLike;

	const tempMat = new PIXI.Matrix();
	const tempRect = new PIXI.Rectangle();
	const tempPoint = new PIXI.Point();

	export class BilinearSurface extends Surface {
		distortion = new PIXI.Point();

		constructor() {
			super();
		}

		clear() {
			this.distortion.set(0, 0);
		}

		apply(pos: PointLike, newPos?: PointLike): PointLike {
			newPos = newPos || new PIXI.Point();
			const d = this.distortion;
			const m = pos.x * pos.y;
			newPos.x = pos.x + d.x * m;
			newPos.y = pos.y + d.y * m;
			return newPos;
		}

		applyInverse(pos: PointLike, newPos: PointLike): PointLike {
			newPos = newPos || new PIXI.Point();
			const vx = pos.x, vy = pos.y;
			const dx = this.distortion.x, dy = this.distortion.y;

			if (dx == 0.0) {
				newPos.x = vx;
				newPos.y = vy / (1.0 + dy * vx);
			} else
			if (dy == 0.0) {
				newPos.y = vy;
				newPos.x = vx/ (1.0 + dx * vy);
			} else {
				const b = (vy * dx - vx * dy + 1.0) * 0.5 / dy;
				const d = b * b + vx / dy;

				if (d <= 0.00001) {
					newPos.set(NaN, NaN);
					return;
				}
				if (dy > 0.0) {
					newPos.x = - b + Math.sqrt(d);
				} else {
					newPos.x = - b - Math.sqrt(d);
				}
				newPos.y = (vx / newPos.x - 1.0) / dx;
			}
			return newPos;
		}

		mapSprite(sprite: PIXI.Sprite, quad: Array<PointLike>, outTransform?: PIXI.TransformStatic) {
			const tex = sprite.texture;

			tempRect.x = -sprite.anchor.x * tex.orig.width;
			tempRect.y = -sprite.anchor.y * tex.orig.height;
			tempRect.width = tex.orig.width;
			tempRect.height = tex.orig.height;

			return this.mapQuad(tempRect, quad, outTransform || sprite.transform as PIXI.TransformStatic);
		}

		mapQuad(rect: PIXI.Rectangle, quad: Array<PointLike>, outTransform: PIXI.TransformStatic) {
			const ax = -rect.x / rect.width;
			const ay = -rect.y / rect.height;

			const ax2 = (1.0 - rect.x) / rect.width;
			const ay2 = (1.0 - rect.y) / rect.height;

			const up1x = (quad[0].x * (1.0 - ax) + quad[1].x * ax);
			const up1y = (quad[0].y * (1.0 - ax) + quad[1].y * ax);
			const up2x = (quad[0].x * (1.0 - ax2) + quad[1].x * ax2);
			const up2y = (quad[0].y * (1.0 - ax2) + quad[1].y * ax2);

			const down1x = (quad[3].x * (1.0 - ax) + quad[2].x * ax);
			const down1y = (quad[3].y * (1.0 - ax) + quad[2].y * ax);
			const down2x = (quad[3].x * (1.0 - ax2) + quad[2].x * ax2);
			const down2y = (quad[3].y * (1.0 - ax2) + quad[2].y * ax2);

			const x00 = up1x * (1.0 - ay) + down1x * ay;
			const y00 = up1y * (1.0 - ay) + down1y * ay;

			const x10 = up2x * (1.0 - ay) + down2x * ay;
			const y10 = up2y * (1.0 - ay) + down2y * ay;

			const x01 = up1x * (1.0 - ay2) + down1x * ay2;
			const y01 = up1y * (1.0 - ay2) + down1y * ay2;

			const x11 = up2x * (1.0 - ay2) + down2x * ay2;
			const y11 = up2y * (1.0 - ay2) + down2y * ay2;

			const mat = tempMat;
			mat.tx = x00;
			mat.ty = y00;
			mat.a = x10 - x00;
			mat.b = y10 - y00;
			mat.c = x01 - x00;
			mat.d = y01 - y00;
			tempPoint.set(x11, y11);
			mat.applyInverse(tempPoint, tempPoint);
			this.distortion.set(tempPoint.x - 1, tempPoint.y - 1);

			outTransform.setFromMatrix(mat);

			return this;
		}

		fillUniforms(uniforms: any) {
			uniforms.distortion = uniforms.distortion || new Float32Array([0, 0]);
			const ax = Math.abs(this.distortion.x);
			const ay = Math.abs(this.distortion.y);

			uniforms.distortion[0] = ax * 10000 <= ay ? 0 : this.distortion.x;
			uniforms.distortion[1] = ay * 10000 <= ax ? 0 : this.distortion.y;
		}
	}
}
